@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "MyriadPro-It";
  src: url("../fonts/myriadpro-it.woff2"), url("../fonts/myriadpro-it.woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: "MyraidPro";
  src: url("../fonts/MyriadPro-Regular.woff2"), url("../fonts/MyriadPro-Regular.woff");
  font-size: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: "MyraidPro";
  src: url("../fonts/MyriadPro-Semibold.woff2"), url("../fonts/MyriadPro-Semibold.woff");
  font-size: normal;
  font-weight: 600;
  font-display: swop;
}

@font-face {
  font-family: "MyraidPro";
  src: url("../fonts/MyriadPro-Bold.woff2"), url("../fonts/MyriadPro-Bold.woff2");
  font-size: normal;
  font-weight: 700;
  font-display: swap;
}

html,
body {
  font-size: 16px;
  max-width: 1400px;
  font-family: MyraidPro, san-serif;
  margin: 0 auto;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.container {
  max-width: 1198px;
  margin: 0 auto;
  background-color: #ffffff;
}

main {
  background-color: #151515;
}

.title {
  font-size: 30px;
  line-height: 1.2;
  font-weight: 600;
}

.title:after {
  content: "";
  display: block;
  width: 133px;
  height: 1px;
  background-color: #c5c5c5;
  margin-left: 105px;
  margin-top: -7px;
}

.title-yellow {
  color: #ffc600;
  font-size: 50px;
}

.title-big {
  font-size: 50px;
}

.text-it {
  font-family: MyriadPro-It, san-serif;
  font-size: 1em;
  color: #000000;
}

.text-pro {
  font-family: MyraidPro, san-serif;
  font-size: 14px;
  line-height: 1.2;
  color: #595959;
}

/*  header start  */
.header {
  max-width: 1440px;
  position: relative;
}

.header .header__baner {
  max-height: 1086px;
}

.header .header__baner img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center;
}

.header__content {
  width: 665px;
  position: absolute;
  top: 120px;
  left: 135px;
}

.header__title {
  font-size: 202.2px;
  font-weight: 700;
  letter-spacing: -0.05em;
  color: #ffffff;
  margin-left: -9px;
}

.header__title .header__title_yellow {
  color: #ffc600;
  text-transform: uppercase;
}

.header__text {
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
  line-height: 1.2;
  margin-top: -30px;
  margin-bottom: 30px;
}

.header__text .header__text_yellow {
  color: #ffc600;
  text-transform: uppercase;
}

.header__download {
  font-size: 18px;
  font-weight: 400;
  color: #ffc600;
}

/*  второе фото хедера   */
.aside {
  min-width: 100%;
  position: absolute;
  bottom: 0;
}

.aside .aside__img {
  position: relative;
  max-width: 1198px;
  margin-left: auto;
  margin-right: auto;
}

.aside .aside__img img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center;
}

.aside__content {
  position: absolute;
  top: 80px;
  left: 133px;
}

.aside__title {
  font-size: 60px;
  line-height: 48px;
  font-weight: 700;
  letter-spacing: -0.05em;
  color: #ffffff;
}

.aside__title_yellow {
  color: #ffc600;
}

.aside__menu {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  width: 355px;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
}

.menu__link {
  transition: 0.5s;
  color: #ffc600;
  border-bottom: 2px solid rgba(255, 198, 0, 0);
}

.menu__link:first-letter {
  text-transform: uppercase;
}

.menu__link:hover {
  transition: 0.5s;
  color: #ffffff;
  border-bottom: 2px solid #ffc600;
}

/*  header and  */
/* ? ---  about start --- */
.about {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  line-height: 1.2;
  padding-left: 15px;
  padding-right: 15px;
}

.about__header {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.about__title {
  max-width: 50%;
  padding-right: 185px;
  transform: translateY(45px);
  font-size: 80px;
  font-weight: 600;
  color: #000000;
}

.about__title:after {
  content: "";
  width: 133px;
  height: 1px;
  display: block;
  background-color: #717171;
  transform: translate(150px, -20px);
}

.about__title .about__title_yellow {
  color: #ffc600;
}

.about__tetle_gray {
  font-weight: 600;
  color: #717171;
  margin-bottom: -30px;
}

.about__tetle_gray .about__tetle_gray50 {
  font-size: 50px;
}

.about__tetle_gray .about__tetle_gray30 {
  font-size: 30px;
}

.about__text {
  max-width: 50%;
}

.about__text .about__text_it {
  padding-top: 2.37em;
  padding-bottom: 1.56em;
}

.about__text .about__text_pro:after {
  content: "";
  width: 133px;
  height: 1px;
  display: block;
  background: #717171;
  transform: rotate(90deg);
  position: relative;
  top: -90px;
  left: -100px;
}

.about__footer {
  display: flex;
  justify-content: center;
}

.about__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 900px;
}

.about__directions {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 250px;
  padding-top: 60px;
}

.directions__title {
  font-size: 18px;
  padding: 26px 0;
}

.directions__text {
  font-family: MyriadPro-It, san-serif;
  font-size: 10px;
  text-align: center;
}

.directions__text p:first-child {
  margin-bottom: 20px;
}

.directions__icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 57px;
  height: 57px;
  border-radius: 50%;
  border: 2px solid #ffa800;
  background-color: #e8e8e8;
}

.directions__icon img {
  max-width: 30px;
  max-height: 20px;
}

/* ? ---  about and --- */
/*  featured start  */
.featured {
  padding-top: 70px;
}

.featured__wrapper {
  padding-left: 82px;
}

.featured__list {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
}

.featured__list li:after {
  content: "";
  width: 23px;
  height: 1px;
  display: block;
  background-color: rgba(255, 198, 0, 0);
  transition: 0.7s;
}

.featured__list li li:nth-child(2) {
  margin-left: 1em;
}

.featured__list li:hover:after {
  content: "";
  width: 23px;
  height: 1px;
  display: block;
  background-color: #ffc600;
  transition: 0.7s;
}

.featured__link {
  font-size: 9px;
  line-height: 1.2;
  font-weight: 600;
  color: #000000;
  text-transform: uppercase;
  display: inline-block;
  padding-right: 1em;
}

.featured__gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.featured__gallery-img {
  width: 299.5px;
  height: 267px;
}

.featured__gallery-img img {
  max-width: 100%;
  min-height: 100%;
  object-fit: cover;
  object-position: center;
}

/*  featured and  */
/* ? ---  strategic start --- */
.strategic {
  padding-top: 65px;
  padding-right: 20px;
  padding-left: 20px;
  margin-top: -1px;
}

.strategic__wrapper {
  max-width: 1055px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 32px;
}

.strategic__text_it {
  padding: 1.8em 0;
}

/* ? ---  strategic and --- */
/* process start */
.process {
  max-width: 860px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.process__wrapper {
  width: 355px;
  margin: 15px auto;
}

.process__img {
  max-width: 50%;
  max-height: 288px;
}

.process__img img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: center;
}

.process__content {
  min-width: 50%;
}

.process__title:after {
  display: none;
}

.text-it {
  padding-bottom: 25px;
}

/* process and */
/*  ? ---  services start --- */
.services {
  min-width: 1198px;
  min-height: 478px;
  position: relative;
  color: #ffffff;
  padding-top: 124px;
}

.services__img {
  background-image: url("../img/services.jpg");
  background-repeat: no-repeat;
  background-position: top;
  overflow: hidden;
  object-fit: cover;
  max-width: 100%;
  height: 460px;
}

.services__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: absolute;
  top: 124px;
  min-width: 1030px;
}

.services__title {
  padding-left: 50px;
}

.services__title:after {
  background-color: #ffffff;
  margin-left: 120px;
}

.services__content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 542px;
  padding-top: 15px;
}

.services__content-block {
  font-size: 11px;
  line-height: 1.2;
  font-weight: 400;
  width: 225px;
}

.services__content-block h5 {
  font-size: 18px;
  margin-bottom: 30px;
  margin-top: 77px;
}

/*  ? ---  services  and --- */
/* ? ---  team start --- */
.team {
  display: flex;
  flex-wrap: wrap;
  padding: 85px 70px 153px 50px;
  justify-content: space-between;
}

.team__title::after {
  content: "";
  display: block;
  width: 85px;
  height: 1px;
  margin-left: 60px;
}

.team__staff {
  min-width: 789px;
  padding-left: 10px;
}

.staff__text-it {
  max-width: 630px;
  font-size: 20px;
  padding-bottom: 14px;
}

.team__foto {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 782px;
  padding-top: 27px;
}

.foto__text {
  text-align: center;
  margin-top: 16px;
}

.foto__developer img {
  background-color: #1d1d1d;
  min-width: 181px;
  min-height: 186px;
  object-fit: cover;
  object-position: center;
}

.foto__text-name {
  font-family: MyraidPro, san-serif;
  line-height: 21px;
}

.foto__text-position {
  font-family: MyriadPro-It, san-serif;
  letter-spacing: 1.5px;
}

/* ? ---  team and --- */
/* ? ---  test start --- */
.test {
  background-color: #000000;
  display: flex;
  flex-wrap: wrap;
  color: #ffffff;
  justify-content: space-between;
}

.test__text {
  max-width: 50%;
  margin: 41px 0 160px 50px;
}

.test__title::after {
  content: "";
  display: block;
  width: 85px;
  height: 1px;
  margin-left: 88px;
}

.test__content {
  max-width: 30%;
}

.test__slogan {
  margin-left: 45px;
  line-height: 21px;
  min-width: 100%;
}

.test__slogan--italics {
  font-family: MyriadPro-It, san-serif;
  font-size: 16px;
  margin-top: 70px;
  margin-bottom: 11px;
}

.test__slogan--normal {
  font-family: MyraidPro, san-serif;
  font-size: 14px;
}

.test__content {
  margin-top: 92px;
  margin-right: 267px;
}

.img__border {
  min-width: 130px;
  min-height: 130px;
  border: 3px solid #e89800;
  border-radius: 50%;
  overflow: hidden;
}

.img__border .test__avatar {
  width: auto;
  object-fit: cover;
  object-position: center;
}

.test__signature {
  margin-top: 20px;
}

.signature__text {
  display: block;
  display: flex;
  justify-content: center;
}

.signature__text--name {
  margin-top: 5px;
}

.signature__text--position {
  margin-top: -10px;
}

/* ? ---  test and --- */
